.WishlistSharedPage {
    &-Products {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        grid-auto-rows: max-content;
        grid-column-gap: 2.4rem;

        @include tablet {
            grid-template-columns: repeat(4, 1fr);
        }

        @include mobile {
            grid-column-gap: 2.8rem;
            grid-template-columns: 1fr;
            border: 0;
        }

        @include after-mobile {
            grid-column: 2;
        }
    }

    &-ActionBar {
        display: flex;
        padding: 0;
        justify-content: flex-end;
        z-index: 1;

        @include after-mobile {
            position: absolute;
            top: -3px;
            right: 0;
        }

        @include mobile {
            margin-top: 1.4rem;
        }
    }

    &-CreatorsInfo {
        margin: 2.4rem 0;
        font-weight: normal;

        @include mobile {

            margin: 1.4rem 0 2.8rem;
            font-size: 2.1rem;
        }
    }
}
